.main-carousel {

  &__inner {
    width: 100vw;
    height: calc(100vh - 17.7rem);
    min-height: 70rem;
    position: relative;
    color: white;
  }

  &__wrap {
    height: 100%;
    width: 100%;
    overflow: hidden;
  }

  &__list {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
  }

  &__title {
    font-size: 5rem;
    margin: 0 0 2rem 0;
  }

  &__subtitle {
    display: inline-block;
    font-size: 2.4rem;
    text-transform: uppercase;
    font-weight: 900;
    padding-bottom: 0.5rem;
    border-bottom: 0.4rem solid @c-red;

    margin-bottom: 1rem;
  }

  &__text {
    max-width: 60rem;
    font-size: 2rem;
    margin: 0 0 1rem 0;
  }

  &__link {
    display: flex;
    align-items: center;

    color: white;
    transition: background-color @time;

    position: relative;

    &-figure {
      position: absolute;
      left: -5rem;
      width: 5.5rem;
      height: 5.5rem;
      background-color: @c-red;
      border-radius: 50%;
      transform: scale(0);
      transition: @time 0.3s;

    }

    svg {
      fill: @c-red;
      position: relative;
      transform: translateX(-2rem);
      z-index: 2;
      margin-right: 1rem;
      width: 5rem;
      height: 1rem;

      transition: @time;

      animation: change-background 1s linear infinite alternate;
    }

    @keyframes change-background {
      0% {
        transform: translateX(0rem);
      }
      50% {
        transform: translateX(-0.5rem);
      }
      100% {
        transform: translateX(-1rem);
      }
    }

    &-text {
      font-size: 1.8rem;
      color: inherit;
      transform: @time;
    }

    &:hover {
      & .main-carousel__link-figure {
        transform: scale(1);
        left: -3rem;
      }

      svg {
        fill: white;
        transform: translateX(0);
        animation: none;
      }

      & .main-carousel__link-text {
        font-weight: 700;
      }

    }

  }

  &__item {
    overflow: hidden;
    flex-shrink: 0;
    position: relative;
    height: 100%;
    width: 100%;
    visibility: hidden;

    &.swiper-slide-active {
      visibility: visible;
    }

    &._blue {
      //background: linear-gradient(249.59deg, #166ffd 11.81%, #001D48 88.22%);
      background-color: #001D48;


    }

    &._orange {
      background: linear-gradient(249.59deg, #F9D423 11.81%, #F89B00 88.22%)
    }

    &._violet {
      background: linear-gradient(249.59deg, #be73ec 11.81%, #683785 88.22%)
    }


    &-outer {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 70%;
      height: 75%;
      padding: 4rem 2rem;
      margin: 0 auto;

      display: flex;
      flex-direction: column;
    }

    &-inner {
      flex-grow: 1;
      height: 50%;
      display: flex;
    }

    &-content {
      width: 50%;
      position: relative;
      z-index: 4;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
    }

  }

  &__thumb {
    width: 10%;
    max-width: 14rem;
    position: absolute;
    top: 3rem;
    right: 5rem;

    &-list {
      transform: none !important;
    }

    &-item {
      max-width: 13.8rem;
      max-height: 14.5rem;
      min-width: 10rem;
      min-height: 9rem;

      width: 100%;
      height: 100%;
      margin-bottom: 3rem;
      opacity: 0.5;
      transition: @time * 2;
      cursor: pointer;

      &:last-child {
        margin-bottom: 0;
      }

      &.swiper-slide-thumb-active {
        opacity: 1;
      }

    }

    &-img {
      display: inline-block;

      img {
        object-fit: contain;
      }

    }

  }

  &__pagination {
    width: 10%;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 5rem;
    transform: translateY(-50%);

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;


    & .swiper-pagination-bullet {
      width: 1.7rem;
      height: 1.7rem;
      border-radius: 50%;
      flex-shrink: 0;
      margin: 1rem;
      background-color: #fff;
      opacity: 0.4;
      cursor: pointer;
      transition: transform @time;
      border: 1px solid #ffffff;

      &-active {
        background-color: #D70000;
        opacity: 1;
        transform: scale(1.5);
        margin: 1.5rem 1rem;
        border: 1px solid transparent;
      }

    }
  }

  &__circle {
    width: 50%;
    position: relative;


    &:before {
      content: none;
      width: 120%;
      padding-top: 120%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
      border-radius: 50%;
    }

    &._blue {
      &:before {
        content: '';
        background: linear-gradient(245.45deg, #16E3DA 12.51%, #46AEF7 87.72%);
      }
    }

    &._orange {
      &:before {
        content: '';
        background-image: linear-gradient(245.45deg, #F89B00 12.51%, #F9D423 87.72%);
      }
    }

    &._violet {
      &:before {
        content: '';
        background-image: linear-gradient(245.45deg, #683785 12.51%, #be73ec 87.72%);
      }
    }

    &-img {
      display: flex;
      width: 100%;
      height: 100%;
      padding: 6rem 3rem;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;



      }

    }
  }

  &__badge {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;

    font-size: 2.4rem;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
  }

}
