.benefits-card {

  &__inner {
    padding: 10rem 0;
  }

  &__title {
    display: inline-block;
    font-weight: 700;
    font-size: 4rem;
    position: relative;
    padding-bottom: 1.5rem;
    margin-bottom: 5rem;
    overflow: hidden;
    transform: translate(0, 120%);
    opacity: 0;
    transition: 0.8s;

    &._active {
      opacity: 1;
      transform: translate(0, 0);
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      height: 1rem;
      width: 70%;
      background-color: @c-red;
    }

  }

  &__wrap {
    padding: 10rem 0;
    overflow: hidden;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    flex-shrink: 0;
    width: calc((100% - 4rem * 4) / 5);
    margin-right: 4rem;
    border-radius: 16px;

    box-shadow: 0px 2rem 4rem rgba(179, 45, 92, 0.2);

    &._purple {
      background: linear-gradient(225deg, #FF057C 0%, #8D0B93 50.51%, #321575 100%);
    }

    &._peachy {
      background: linear-gradient(315deg, #B12A5B 0%, #CF556C 25.38%, #FF8C7F 63.27%, #FF8177 100%);

    }

    &._blue {
      background: linear-gradient(45deg, #00F2FE 0%, #4FACFE 100%);
    }

    &._green {
      background: linear-gradient(45deg, #F4D03F 0%, #16A085 100%);
    }

    &:last-child {
      margin-right: 0;
    }

    &:nth-child(1) {
      transform: translate( 0, 5%);
    }
    &:nth-child(2) {
      transform: translate( 0,-10%);
    }
    &:nth-child(3) {
      transform: translate( 0, 0);
    }
    &:nth-child(4) {
      transform: translate( 0, 10%);
    }
    &:nth-child(5) {
      transform: translate( 0, -5%);
    }

    &-inner {
      color: white;
      padding: 3.5rem;

      display: flex;
      flex-direction: column;
      align-items: center;

      svg {
        max-width: 25rem;
        width: 100%;
        height: 25rem;
      }
  
    }
  }

  &__text {
    font-weight: 700;
    font-size: 2rem;
    margin-bottom: 4rem;
  }

}
