.main-carousel {

  @media (max-width: @xl) {
    &__inner {
      height: calc(100vh - 17.1rem);
    }

    &__item {
      &-outer {
        width: 75%;
      }
    }

    &__title {
      font-size: 3rem;
    }

    &__subtitle {
      font-size: 2rem;
      font-weight: 700;
    }

    &__text {
      font-size: 1.6rem;
    }

    &__badge {
      font-size: 1.4rem;
    }

    &__thumb {
      width: 7%;

      &-item {
        min-width: 5rem;
        min-height: auto;
      }

    }

    &__pagination {
      width: 7%;
      left: 2rem;
    }

  }

}
