.popular-block {

  background-color: @color-light-white;

  &__inner {
    padding: 9rem 0;
    overflow: hidden;
  }

  &__title {
    display: inline-block;
    font-size: 4rem;
    color: @c-primary;
    padding: 2rem;
    position: relative;

    margin-bottom: 2rem;
    overflow: hidden;

    transform: translate( 120%, 0);
    opacity: 0;
    transition: transform @time * 3;

    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      height: 0.3rem;
      width: 100%;
      background-color: @c-red;
    }

    &._active {
      transform: translate(0, 0);
      opacity: 1;
    }

  }

  &__wrap {
    overflow: hidden;

    transform-origin:top;
    transform: translate( -150%, 0);
    transition: transform  @time * 3 ease .3s;

    &._active {
      transform: translate(0, 0) rotate(0);
    }
  }

  &__list {
    display: flex;
    padding-bottom: 5rem;

  }

  &__item {
    flex-shrink: 0;
    width: calc((100% - 3rem * 4)/ 5);

    margin-right: 3rem;

    &:last-child {
      margin-right: 0;
    }

  }

}
