.header {

  @media (max-width: @xl) {

    &__logo {
      margin-right: 3rem;
    }

    &__menu {
     &-link {
       max-width: 19rem;
     }
    }

    &__top-social {
      margin: 0 2rem;
    }

    &__top-menu {
     & .site-navigation__link {
       font-size: 1.2rem;
     }
    }

    &__bottom {
      & .main-menu__item {
        font-size: 1.2rem;
      }
    }
  }

}
