.footer {

  background-color: @c-blue-dark;
  color: white;

  &__title {
    text-transform: uppercase;
    margin-bottom: 2rem;
  }

  &__logo {
    margin-bottom: 2.5rem;
  }

  &__top {
    padding: 2rem 0 4rem;
    margin-left: -1rem;
    margin-right: -1rem;

    display: flex;

    &-col {
      padding: 0 1rem;
      width: 25%;
    }
  }

  &__social {
    margin-bottom: 2rem;
  }

  &__list {
    .resetList;

  }

  &__item {
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    color: inherit;
    display: flex;
    align-items: center;

    svg {
      margin-right: 1.5rem;

      width: 2.5rem;
      height: 2.5rem;
    }

  }

  &__menu-wrap {
    display: flex;
  }

  &__menu {
    width: 50%;
    padding-right: 1.5rem;

    &:last-child {
      padding-right: 0;
    }

    & .site-navigation__item {
      margin-bottom: 1rem;
    }

    & .main-menu__item {
      margin-bottom: 1.5rem;
    }

  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    border-top: 0.1rem solid white;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;

    &-item {
      font-size: 1.4rem;
    }

  }

  &__btn {
    .resetBtn;
    margin-top: 2.5rem;

    background-color: transparent;
    border: .3rem solid white;
    color: white;
    font-weight: 600;
    padding: 1rem 1.5rem;
    border-radius: 1.5rem;

    transition: all @time ease-in-out;

    &:hover {
      box-shadow: 0 0 10px 0 @c-red inset, 0 0 20px 2px @c-red;
      border: 3px solid @c-red;
    }

  }

}
