.link-circle {

  &__link {
    display: flex;
    align-items: center;
    color: white;
    transition: background-color 0.3s;
    position: relative;

    &:hover {
      & .link-circle__figure {
        transform: scale(1);
        left: -3rem;
      }

      svg {
        fill: white;
        transform: translateX(0);
        animation: none;
      }

      & .link-circle__text {
        font-weight: 700;
      }

    }

    &._black {

      & .link-circle__text {
        color: @c-black;
      }

      &:hover {
        svg {
          fill: @c-black;
        }
      }

    }

  }

  &__figure {
    position: absolute;
    left: -5rem;
    width: 5.5rem;
    height: 5.5rem;
    background-color: #EF1616;
    border-radius: 50%;
    transform: scale(0);
    transition: 0.3s 0.3s;
  }

  svg {
    fill: #EF1616;
    position: relative;
    transform: translateX(-2rem);
    z-index: 2;
    margin-right: 1rem;
    width: 5rem;
    height: 1rem;
    transition: 0.3s;
    animation: change-background 1s linear infinite alternate;
  }

  &__text {
    font-size: 1.8rem;
    color: inherit;
    transition: 0.3s;
  }

  @keyframes change-background {
    0% {
      transform: translateX(0rem);
    }
    50% {
      transform: translateX(-0.5rem);
    }
    100% {
      transform: translateX(-1rem);
    }
  }

}

