.btn {
  .resetBtn;
  padding: 1rem 2rem;
  border-radius: 1rem;
  color: white;
  background-color: @c-blue-dark;
  background-image: linear-gradient(45deg, @c-blue 50%, transparent 50%);
  background-position: 100%;
  background-size: 400%;
  transition: background 500ms ease-in-out;

  &:not(._disabled):hover {
    background-color: @c-blue;
    color: white;
    outline: 0;
    background-position: 0;
  }

}
