.our-portfolio {

  &__inner {
    padding: 5rem 0;
  }

  &__title {
    display: inline-block;
    font-weight: 700;
    font-size: 4rem;
    padding-bottom: 2rem;

    margin-bottom: 3rem;

    border-bottom: 1rem solid @c-red;
  }

  &__photo-gallery {
    width: 100%;

    margin: 0 -1rem;
  }

  &__item {
    &:hover {
      & .our-portfolio__img {

        &:before {
          z-index: 2;
          background-color: rgba(0, 0, 0, .5);
        }

        img {
          transform: scale(1.2);
        }

      }

      & .our-portfolio__content {
        opacity: 1;

        &:before,
        &:after {
          opacity: 1;
          transform: translate(-50% , -50%) scale(1)
        }

        &-title {
          transform: scale(1);
        }

        &-text {
          transform: translate(0, 0);
        }

      }

      & .our-portfolio__link {
        transform: scale(1);
      }

    }
  }

  &__top {
    display: flex;
    flex-wrap: wrap;

    &-col {
      padding: 1rem;
      width: 50%;
      max-height: 40rem;

      position: relative;

      &:last-child {
        max-height: 60rem;
        width: 100%;
      }
    }
  }

  &__bottom {
    display: flex;
    max-height: 80rem;


    &-col {
      position: relative;
      padding: 1rem;
      width: 50%;

      display: flex;
      flex-direction: column;
    }

    &-item {
      height: 50%;
      padding-bottom: 1rem;

      position: relative;

      &:last-child {
        padding-bottom: 0;
      }
    }

  }

  &__img {
    display: flex;
    width: 100%;
    height: 100%;
    position: relative;

    overflow: hidden;


    &:before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;

      transition: all @time ease 0s;
    }

    img {
      transition: all @time ease 0s;
      width: 100%;
      object-fit: cover;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    opacity: 0;

    position: absolute;
    //border: 1px solid red;
    left: 50%;
    top: 50%;
    z-index: 2;
    transform: translate(-50% , -50%);
    color: white;
    width: 75%;
    height: 75%;

    &::before,
    &::after {
      content: '';
      position: absolute;
      z-index: 1;
      left: 50%;
      top: 50%;
      opacity: 0;

      transition: all @time ease;
    }

    &::before {
      width: 110%;
      height: 100%;

      transform: translate(-50% , -50%) scale(0,1);

      border-top: 1px solid #fff;
      border-bottom: 1px solid #fff;
      transform-origin: 0 0;
    }

    &::after {
      width: 100%;
      height: 110%;

      transform: translate(-50% , -50%) scale(1,0);
      border-left: 1px solid #fff;
      border-right: 1px solid #fff;
      transform-origin: 100% 0;
    }

    &-title {
      transform: scale(0);
      font-size: 2rem;
      margin-bottom: 1.5rem;

      transition: all @time ease @time;
    }

    &-text {
      transform: translate(0, 120%);
      text-align: center;
      margin-bottom: 1.5rem;

      transition: all @time ease @time * 2;
    }

  }

  &__link {
    position: relative;
    z-index: 4;
    transform: scale(0);
    color: inherit;

    display: flex;
    align-items: center;

    transition: transform @time ease @time * 3;

    svg {
      fill: @c-red;
      width: 5rem;
      height: 2rem;

      &:first-child {
        animation: svg-first 1s linear infinite alternate;

        @keyframes svg-first {
          0% {
            transform: translateX(0rem);
          }
          50% {
            transform: translateX(-0.5rem);
          }
          100% {
            transform: translateX(-1rem);
          }
        }
      }
      &:last-child {
        animation: svg-second 1s linear infinite alternate;

        @keyframes svg-second {
          0% {
            transform: translateX(-1rem);
          }
          50% {
            transform: translateX(-0.5rem);
          }
          100% {
            transform: translateX(0);
          }
        }
      }
    }

    &:hover {
      color: red;

      svg {

        &:first-child {
          transform: translateX(0);
        }

        &:last-child {
          transform: translateX(-1rem);
        }

        animation: none;
      }
    }

  }

}
