.select-dropdown {
  display: inline-flex;
  position: relative;
  font-size: 1.5rem;
  justify-content: center;

  &__trigger {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;

    svg {
      width: 1.1rem;
      height: 1.1rem;
      margin-left: .4rem;
      transition: transform @time;
    }

    span {
      .clipText;
      max-width: 6rem;
      user-select: none;
    }
  }

  &__dropdown {
    position: absolute;
    top: calc(100% + 1rem);
    //display: none;
    transition-property: opacity, visibility, transform;
    transition-duration: @time;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform-origin: center top;
    left: 50%;
    transform: translateX(-50%) translateY(-1rem);
    display: none;
    justify-content: center;
    z-index: 30;
    //filter: drop-shadow(0 1rem 1rem fade(#000000, 50));

    &-close {
      display: inline-flex;
      position: absolute;
      top: 1.5rem;
      right: 1.5rem;
      cursor: pointer;

      svg {
        width: 1.6rem;
        height: 1.6rem;
      }
    }


    &-corner {
      position: absolute;
      display: block;
      border: 1rem solid transparent;
      border-top: 0;
      border-bottom-color: #ffffff;
      border-bottom-width: 1rem;
      left: 50%;
      transform: translateX(-50%);
      bottom: calc(100% - .2rem);
      width: 1rem;
      height: 1rem;
    }

    &-inner {
      background-color: #ffffff;
      padding: 1.5rem 1.6rem 1.2rem;
      border-radius: 1rem;
      box-shadow: 0 0 6rem fade(#000000, 50);
      margin-bottom: 2rem;

      color: @c-black;
    }

  }

  &._active {
    .select-dropdown {
      &__trigger {
        svg {
          transform: rotate(-180deg);
        }
      }

      &__dropdown {
        opacity: 1;
        visibility: visible;
        pointer-events: inherit;
        transform: translateX(-50%);
      }
    }
  }

  &__phones {
    width: 15rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    li {
      font-size: 1.8rem;
      margin-bottom: .5rem;

      &:last-child {
        margin-bottom: 0;
      }

      a {
        &:hover {
          color: @c-red;
        }
      }

    }

  }

}
