.sample-carousel {
  position: relative;

  &__inner {
    padding: 6rem 0;
  }

  &__title {
    font-weight: 700;
    font-size: 4rem;

    max-width: 130rem;
    margin-bottom: 3rem;

  }



  &__wrap {
    overflow: hidden;
    height: 70rem;
  }

  &__list {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
  }

  &__item {
    width: 100%;
    height: 100%;
    flex-shrink: 0;
    padding-bottom: 2rem;
  }

  &__body {
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 2rem;
  }

  &__row {
    flex-grow: 1;
    height: 50%;
    display: flex;

  }

  &__col {
    width: 50%;

    &:first-child {
      padding-right: 5rem;
    }

  }

  &__content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__img {
    display: flex;
    width: 100%;
    height: 100%;

    background-image: url("../img/svg/circle.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: top right;

    img {
      margin: 0 auto;
      object-fit: contain;

    }
  }

  &__line {
    display: inline-block;
    width: 80%;
    height: 1rem;
    background-color: @c-red;
  }

  &__text {
    margin: 2rem 0;

    p {
      margin: 0;
      font-size: 1.8rem;
    }
  }

  &__link {
    padding: 2rem 3rem;
    align-self: flex-start;
  }

  &__pagination {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 2rem;
    transform: translateY(-50%);

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;


    & .swiper-pagination-bullet {
      width: 1.7rem;
      height: 1.7rem;
      border-radius: 50%;
      flex-shrink: 0;
      margin: 1rem;
      background-color: @c-gray-light;
      opacity: 0.4;
      cursor: pointer;
      transition: transform @time;
      border: 1px solid #ffffff;

      &-active {
        background-color: #D70000;
        opacity: 1;
        transform: scale(1.5);
        margin: 1.5rem 1rem;
        border: 1px solid transparent;
      }

    }
  }

}
