.header {

  background-color: #001D48;
  color: white;

  & .main-menu__link {
    position: relative;
    padding-bottom: 1rem;
    transition: @time;

    &:before {
      content: '';
      position: absolute;
      z-index: 4;
      bottom: 0;
      background-color: transparent;
      height: 0.2rem;
      width: 0;
    }

    &:hover {
      &:before {
        width: 100%;
        background-color: @c-red;
        transform: translateY(0);
      }
    }

  }

  &__inner {
    padding: 1.5rem 0;
  }

  &__top {
    display: flex;
    align-items: center;

    margin-bottom: 1.5rem;

    &-menu {

      margin-right: auto;

      & .site-navigation__list {
        display: flex;
      }

      & .site-navigation__item {
        margin-right: 3rem;

        &:last-child {
          margin-right: 0;
        }

      }

      & .site-navigation__link {
        text-transform: uppercase;
      }

    }

  }

  &__top-social {
    margin: 0 6rem;
  }

  &__logo {
    display: inline-flex;
    flex-shrink: 0;
    position: relative;
    align-self: center;

    margin-right: 5rem;

    &-inner {
      display: flex;
    }

    img {
      object-fit: contain;
      width: 20rem;
      max-height: 11rem;
    }

  }

  &__content {
    display: flex;
    margin-bottom: 2rem;

    &-items {
      flex-grow: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 -1rem;
    }
  }

  &__menu {
    flex-grow: 1;
    margin-left: auto;

    &-items {
      margin: 0 1rem;
    }

    &-top {
      max-width: 130rem;
      margin-left: auto;
      display: flex;
      align-items: center;
      justify-content: space-between;

      margin-bottom: 1.5rem;
    }

    &-ico {
      margin-right: 1rem;

      svg {
        max-width: 3.6rem;
        height: 3.6rem;

        animation: head-ico-svg 5s linear infinite alternate;
      }

      &-phone {
        margin-right: 1.5rem;

        svg {
          width: 3rem !important;
          height: 3.6rem !important;
          transform: rotate(0) !important;
        }
      }

      @keyframes head-ico-svg {
        0% {
          //transform: scale(0.9);
          color: #bb29cb;
          fill: #bb29cb;
        }
        50% {
          //transform: scale(0.95);
          color: #56c8ee;
          fill: #56c8ee;
        }
        100% {
          //transform: scale(1);
          color: #99cf53;
          fill: #99cf53;
        }
      }
    }

    &-link {
      display: flex;
      align-items: center;
      max-width: 23rem;
      color: inherit;

      svg {
        max-width: 3.6rem;
        height: 3.6rem;
      }
    }

    &-list {
      font-size: 1.4rem;
    }

    &-write-size {
      .resetBtn;
      background: linear-gradient(45deg, #c4036b, #ff0000);
      padding: 1rem 1.5rem;
      border-radius: 1.5rem;
      position: relative;

      transition: @time;
      overflow: hidden;

      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        transition: all @time * 2;
        opacity: 1;
        transform: translate(-105%, 0);
        background-color: rgba(255, 255, 255, 0.8);
      }

      &:hover {
        &:before {
          opacity: 0;
          transform: translate(0, 0);
        }
      }

      svg {
        margin-right: 2rem;

        height: 2.5rem;
        width: 2.5rem;
      }

    }

    &-bottom {
      & .main-menu__list {
        display: flex;
        justify-content: space-between;

        user-select: none;
      }

      & .main-menu__link {
        text-transform: uppercase;
      }

    }

  }

  &__bottom {
    & .main-menu__list {
      display: flex;
      justify-content: space-between;
      margin: 0 0.5rem;
    }

    & .main-menu__item {
      margin: 0 0.5rem;
    }

    & .main-menu__link {
      text-transform: uppercase;
    }

  }

}
