.sample-carousel {

  @media (max-width: @xl) {
    &__title {
      font-size: 3rem;
      margin-bottom: 2rem;
    }

    &__body {
      padding: 2rem 6rem;
    }

    &__col {
      &:first-child {
        padding-right: 3rem;
      }
    }

    &__text {
      p {
        font-size: 1.6rem;
      }
    }

    &__pagination {
      left: 1rem;
    }

  }

}
