.product-card {

  &__inner {
    box-shadow: 0 .6rem 1.6rem rgba(112, 125, 163, 0.1);
    border-radius: 2rem;
    padding: 1.5rem;
    background-color: white;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__badge {
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    background-color: @c-red;
  color: white;
  position: absolute;
    z-index: 2;
    left: 1rem;
    top: 1rem;
  }

  &__text {
    &.text {
      p {
        text-align: center;
      }
    }
  }

  &__img {
    display: block;
    position: relative;
    width: 100%;

    margin-bottom: 2.5rem;

    &:before {
      content: '';
      width: 100%;
      display: block;
      padding-top: 100%;
    }

    img {
      object-fit: contain;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    font-size: 2.4rem;
    letter-spacing: .05rem;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 2.5rem;
  }

  &__text {
    margin-bottom: 2rem;
  }

  &__footer {
    width: 100%;

    display: flex;
    align-items: center;
  }

  &__price {
    display: inline-block;
    font-weight: 700;
    font-size: 2rem
  }

  &__btn {
    margin-left: auto;
  }

}
