.social {

  &__list {
    .resetList;
    display: flex;
    align-items: center;
  }

  &__item {
    flex-shrink: 0;
    margin-right: 1.5rem;

    opacity: 0.5;
    transition: @time;

    &:last-child {
      margin-right: 0;
    }

    svg {
      fill: white;
      width: 2.5rem;
      height: 2.5rem;
    }

    &:hover {
      opacity: 1;
    }

  }


}
