.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 150;
  //overflow: hidden;
  overflow-x: hidden;
  overflow-y: auto;
  @supports (-webkit-overflow-scrolling: touch) {
    -webkit-overflow-scrolling: touch
  }
  //background-color: fade(black, 20);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-property: opacity, visibility;
  transition-duration: @time;
  //display: flex;
  display: none;
  background: fade(@c-black, 80);
  flex-direction: column;


  &._active {
    opacity: 1;
    visibility: visible;
    pointer-events: inherit;

    .modal__wrap {
      transform: none;
    }
  }

  &._dev {
    opacity: 1;
    visibility: visible;
    pointer-events: inherit;
    display: flex;
  }

  &__close {
    .resetBtn();
    align-items: center;
    justify-content: center;
    width: 4.8rem;
    height: 4.8rem;
    border-radius: 50%;
    flex-shrink: 0;
    background-color: white;
    color: @c-black;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    transition: @time;

    svg {
      width: 1.8rem;
      height: 1.8rem;
    }

    &:hover {
      color: @c-red;
    }

  }

  &__title {
    margin: 0;

    font-weight: bold;
    font-size: 2.4rem;
    line-height: 2.6rem;
    text-transform: none;
  }

  &__wrap {
    margin: auto;
    position: relative;
    padding: 6rem @side-offset 6rem;
    max-width: 100%;
    transition-property: transform;
    transition-duration: @time*2;
    transform: scale(.96);
    border-radius: 1rem;
    //max-height: 100%;
    //display: flex;
    //flex-direction: column;
  }

  &__content {
    max-width: 100%;
    width: @container;
    position: relative;

    //overflow-x: hidden;
    //overflow-y: auto;
  }

  &__content-inner {
    padding: 0;
  }

  // SIMPLE

  &._simple &__content {
    width: 39rem;
    padding: 3rem;
    background: white;
  }

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 3rem;
  }

  &._simple &__close {
    width: 2.4rem;
    height: 2.4rem;
    position: static;
    background: transparent;
    align-self: flex-start;
    color: @color-border;
  }
}
